.menu-dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  width: 200px;
  background-color: #333;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.menu-dropdown--active {
  opacity: 1;
  visibility: visible;
}
.menu-dropdown__link {
  display: block;
  width: 100%;
  padding: 10px 10px;
  color: #fff;
}